//////HERO Animation//////////////////////////////////////////////////////////////////////////////////////////////////////

.del {
  font-family: 'Covered By Your Grace', cursive;
  font-size: 1.2em;
  position: relative;
}
@keyframes show {
  from {
    color: transparent;
  }

  to {
    color: inherit;
  }
}
@keyframes showlead {
  from {
    color: transparent;
  }

  to {
    color: inherit;
  }
}

.overlay {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: transparent;
  animation: wave 2s ease-out 1;
  animation-direction: normal;
  z-index: 2;
  cursor: pointer;
}
@keyframes wave {
  0% {
    background-color: black;
  }

  100% {
    background-color: transparent;
  }
}
