//////////////////////////////Media Queries/////////////////////////////////////
@media (min-width:700px) {
  .del:after {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 0;
    width: 0;
    height: 5px;
    background: black;
    border-radius: 30px;
    animation: del 0.5s ease-in-out 2s 1;
    animation-fill-mode: forwards;
  }
  @keyframes del {
    0% {
      width: 0;
    }

    100% {
      width: 100%;
    }
  }

  .show {
    color: transparent;
    animation-name: show;
    animation-duration: 6s;
    animation-delay: 5s;
    animation-fill-mode: forwards;
  }

  .opener {
    animation: slide 0.5s ease-in-out 4s 1;
    animation-fill-mode: backwards;
    position: relative;
  }
  @keyframes slide {
    from {
      right: -8%;
    }

    to {
      right: 0;
    }
  }

  .lead {
    animation: showlead 1.5s ease-out 8s 1;
    animation-fill-mode: backwards;
  }
}
@media (max-width:699px) {
  .del {
    display: none;
  }

  .show {
    color: transparent;
    animation-name: show;
    animation-duration: 4s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
  }

  .opener {
    animation: slide 0.5s ease-in-out 1s 1;
    animation-fill-mode: backwards;
    position: relative;
  }
  @keyframes slide {
    from {
      right: -25%;
    }

    to {
      right: 0;
    }
  }

  .lead {
    animation: showlead 1.5s ease-out 2s 1;
    animation-fill-mode: backwards;
  }
}
@media (max-width:417px) {
  opener {
    animation: slide 0.5s ease-in-out 1s 1;
    animation-fill-mode: backwards;
    position: relative;
  }
  @keyframes slide {
    from {
      right: 0;
    }

    to {
      right: 0;
    }
  }
}